import { Space, Table, Button, message } from 'antd';

const columns = [
    {
        title: '',
        dataIndex: 'id',
        key: 'id',
    },
    {
        title: '',
        dataIndex: 'question',
        key: 'question',
        render: (text) => <span style={{ color: '#1890ff', cursor: 'pointer' }}>{text}</span>,
    },
    {
        title: '',
        dataIndex: 'options',
        key: 'options',
        render: (options) => (
            <span style={{ maxWidth: '400px', display: 'inline-block' }}>
                {options.join(' | ')}
            </span>
        )
    },
    {
        title: '',
        dataIndex: 'answer',
        key: 'answer',
        render: (answer, record) => {
            // 
            const answerIndex = answer.charCodeAt(0) - 65; // A=0, B=1, C=2, D=3
            const answerContent = record.options[answerIndex] || answer;
            return <span style={{ color: '#000', fontWeight: 'normal' }}>{answerContent}</span>;
        },
    },
    {
        title: '',
        key: 'action',
        render: (_, record) => (
            <Space size="middle">
                <Button 
                    type="link" 
                    size="small" 
                    onClick={() => {
                        message.info('');
                        console.log('', record);
                    }}
                >
                    
                </Button>
                <Button 
                    type="link" 
                    size="small" 
                    danger 
                    onClick={() => {
                        message.success('');
                        console.log('', record);
                    }}
                >
                    
                </Button>
            </Space>
        ),
    },
];

const data = [
    {
        key: '1',
        id: '1',
        question: 'React',
        options: ['A. ', 'B. ', 'C. ', 'D. '],
        answer: 'A'
    },
    {
        key: '2',
        id: '2',
        question: 'React',
        options: ['A. componentDidMount', 'B. componentDidUpdate', 'C. componentWillUnmount', 'D. render'],
        answer: 'A'
    },
    {
        key: '3',
        id: '3',
        question: 'React',
        options: ['A. Redux', 'B. MobX', 'C. Context API', 'D. Zustand'],
        answer: 'A'
    },
    {
        key: '4',
        id: '4',
        question: 'React',
        options: ['A. ', 'B. ', 'C. ', 'D. '],
        answer: 'A'
    },
    {
        key: '5',
        id: '5',
        question: 'React',
        options: ['A. React Router', 'B. Next.js', 'C. Reach Router', 'D. Gatsby'],
        answer: 'A'
    }
];

const onChange = (pagination, filters, sorter, extra) => {
    console.log('params', pagination, filters, sorter, extra);
};

const QuestionTable = () => (
    <Table
        columns={columns}
        dataSource={data}
        pagination={{
            current: 1,
            pageSize: 5,
            total: 100,
            showSizeChanger: false,
            position: ['bottomLeft'],
            onChange: () => { /*  */ },
        }}
        onChange={onChange}
    />
);

export default QuestionTable;