import { Form, Input, Button, message, Space } from 'antd';

const formItemLayout = {
  labelCol: { xs: { span: 24 }, sm: { span: 6 } },
  wrapperCol: { xs: { span: 24 }, sm: { span: 18 } },
};

const AddUser = ({ onSuccess }) => {
  const [form] = Form.useForm();
  
  const onFinish = (values) => {
    console.log(': ', values);
    // API
    // API
    setTimeout(() => {
      message.success('');
      form.resetFields();
      if (onSuccess) {
        onSuccess();
      }
    }, 500);
  };

  const onCancel = () => {
    form.resetFields();
    if (onSuccess) {
      onSuccess();
    }
  };

  return (
    <Form
      {...formItemLayout}
      form={form}
      name="addUser"
      onFinish={onFinish}
      scrollToFirstError
      style={{ marginTop: 16 }}
    >
      <Form.Item
        name="username"
        label=""
        rules={[{
          required: true,
          message: '',
        }, {
          min: 3,
          message: '3',
        }]}
      >
        <Input placeholder="" />
      </Form.Item>

      <Form.Item
        name="password"
        label=""
        rules={[{
          required: true,
          message: '',
        }, {
          min: 6,
          message: '6',
        }]}
        hasFeedback
      >
        <Input.Password placeholder="" />
      </Form.Item>

      <Form.Item
        name="confirm"
        label=""
        dependencies={['password']}
        hasFeedback
        rules={[{
          required: true,
          message: '',
        }, ({ getFieldValue }) => ({
          validator(_, value) {
            if (!value || getFieldValue('password') === value) {
              return Promise.resolve();
            }
            return Promise.reject(new Error(''));
          },
        })]}
      >
        <Input.Password placeholder="" />
      </Form.Item>

      <Form.Item wrapperCol={{ offset: 6, span: 18 }}>
        <Space>
          <Button type="primary" htmlType="submit">
            
          </Button>
          <Button onClick={onCancel}>
            
          </Button>
        </Space>
      </Form.Item>
    </Form>
  );
};

export default AddUser;